import { useNavigate } from "react-router-dom";
import styled, { keyframes } from "styled-components";
import { rgba } from "polished";

interface MoreButtonProps {
    link: string;
    style: object;
    color: string;
    children: string;
    props: object;
}

const sheen = keyframes`
    0% { transform: skewX(-45deg) translateX(0); }
    20% { transform: skewX(-45deg) translateX(12.5em); }
    100% { transform: skewX(-45deg) translateX(12.5em); }
`;

const Span = styled.span`
    display: block;
    text-align: center;
    font-size: 1vw;
    font-family: SCDream5;
    border: ${(props) => `1px solid ${props.color};`};
    border-radius: 20px;
    cursor: pointer;
    color: ${(props) => props.color};
    width: max-content;
    min-width: 35px;
    padding: 1px 9px;
    align-items: center;

    transition: all 0.2s ease-in-out;
    overflow: hidden;
    &:before {
        content: "";
        background-color: ${(props) => rgba(props.color, 0.5)};
        height: 100%;
        width: 3em;
        display: block;
        position: absolute;
        top: 0;
        left: -4.5em;
        transform: skewX(-45deg) translateX(0);
        transition: none;
    }
    &:hover {
        color: ${(props) => props.color};
        &:before {
            animation: ${sheen} 3s ease-in-out infinite;
        }
    }

    @media screen and (max-width: 750px) {
        font-size: 3vw;
    }
`;

export default function MoreButton(props: MoreButtonProps) {
    const navigate = useNavigate();
    const gotoPage = () => {
        navigate(props.link || "/");
    };
    const color = props.color || "#000";

    return (
        <Span onClick={gotoPage} style={{ ...props.style }} {...props.props} color={color}>
            {props.children}
        </Span>
    );
}
