import { useMemo } from "react";
import { Route, BrowserRouter, Routes, HashRouter } from "react-router-dom";
import "./css/App.scss";
import Home from "./page/Home.tsx";
import Introduction from "./page/Introduction.tsx";
import Business from "./page/Business.tsx";
import History from "./page/History.tsx";
import Location from "./page/Location.tsx";
import Grip from "./page/Grip.tsx";
import NX from "./page/NX.tsx";
import Teamcenter from "./page/Teamcenter.tsx";
import PlusView from "./page/PlusView.tsx";
import PFMS from "./page/PFMS.tsx";
import PCMS from "./page/PCMS.tsx";
import Service from "./page/Service.tsx";
import Header from "./component/Header.tsx";
import Footer from "./component/Footer.tsx";
import FloatingTopButton from "./component/FloatingTopButton.tsx";
import MobileHeader from "./component/MobileHeader.tsx";
import WrapperContext from "./component/WrapperContext.tsx";
import MenuList from "./assets/menu.json";

const App = () => {
    const headerData = useMemo(() => MenuList, []);

    return (
        <BrowserRouter basename="/">
            <Header data={headerData} />
            <MobileHeader data={headerData} />
            <WrapperContext headerData={headerData}>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="introduction">
                        <Route path="company" element={<Introduction />} />
                        <Route path="business" element={<Business />} />
                        <Route path="history" element={<History />} />
                        <Route path="location" element={<Location />} />
                    </Route>
                    <Route path="production">
                        <Route path="grip" element={<Grip />} />
                        <Route path="nx" element={<NX />} />
                        <Route path="teamcenter" element={<Teamcenter />} />
                        <Route path="plusview" element={<PlusView />} />
                        <Route path="pfms" element={<PFMS />} />
                        <Route path="pcms" element={<PCMS />} />
                    </Route>
                    <Route path="service" element={<Service />} />

                    <Route path="*" element={<Home />} />
                </Routes>
            </WrapperContext>
            <Footer />
            <FloatingTopButton />
        </BrowserRouter>
    );
};

export default App;
