import { useEffect } from "react";
import styled from "styled-components";
import PageTemplate from "../component/PageTemplate.tsx";
import ImgBanner from "../assets/img/introduction/banner.png";

import { useContext } from "react";
import { WrapperContext } from "../component/WrapperContext.tsx";

const Main = styled.div`
    width: 100%;
    height: 100%;
    text-align: left;
    padding: ${(props) => (!props.isMobile ? "25px 0 0 10px" : "25px 0 0 0")};
`;

const Title = styled.span`
    display: block;
    width: max-content;
    max-width: ${(props) => (!props.isMobile ? "100%" : "fit-content")};
    margin-left: 10px;
    font-family: SCDream5;
    font-size: 17px;
`;

const Map = styled.div`
    width: ${(props) => (!props.isMobile ? "700px" : "100%")};
    height: 400px;
    margin-top: 10px;
    margin-bottom: ${(props) => (!props.isMobile ? "50px" : "30px")};
`;
export default function Location() {
    const { isMobile } = useContext(WrapperContext);

    // 시작시 네이버 맵 init
    useEffect(() => {
        const map = new naver.maps.Map("naver-map", {
            center: new naver.maps.LatLng(37.4059657, 127.0857037),
            zoom: 17,
            zoomControl: true,
        });
        const breweryMarker = new naver.maps.Marker({
            position: new naver.maps.LatLng(37.4059657, 127.0857037),
            map: map,
        });
    }, []);

    return (
        <PageTemplate bannerImage={ImgBanner}>
            <Main isMobile={isMobile}>
                <Title isMobile={isMobile}>경기 성남시 수정구 금토동 422-1 판교이노베이션랩 1212호</Title>
                <Map id="naver-map" isMobile={isMobile} />
            </Main>
        </PageTemplate>
    );
}
