import styled from "styled-components";
import { useState, useEffect } from "react";

interface DonutAnimation {
    style: {
        top: string;
        left: string;
        width: string;
        height: string;
        fontSize: string;
    };
    maxValue: number;
    innerColor: string;
    outerColor: string;
    isHideText: boolean;
}

const Chart_Container = styled.div`
    position: absolute;
`;
const Chart = styled.div`
    width: 100%;
    height: 100%;
    position: relative;
    display: inline-block;
    border-radius: 50%;
    transition: 2.3s;

    @media screen and (max-width: 750px) {
        width: 120%;
        height: 120%;
    }
`;
const CenterCircle = styled.span`
    background: #fff;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70%;
    height: 70%;
    border-radius: 50%;
    transform: translate(-50%, -50%);
`;
const Text = styled.span`
    position: absolute;
    top: 52%;
    left: 50%;
    font-size: 70%;
    transform: translate(-50%, -50%);
`;

export default function DonutAnimation(prop: DonutAnimation) {
    const [chartText, setChartText] = useState(0);
    const maxValue = prop.maxValue || 100;
    const innerColor = prop.innerColor || "#ccc";
    const outerColor = prop.outerColor || "#92bb23";
    const isHideText = prop.isHideText || false;

    // 애니메이션
    useEffect(() => {
        let i = maxValue;
        const callback = () => {
            if (i <= maxValue) {
                const step = i / 35; // 숫자가 커지면 느려짐
                i = i - step;
                const val = Math.ceil(maxValue - i);
                setChartText(val);

                requestAnimationFrame(callback);
            }
        };
        requestAnimationFrame(callback);
    }, []);

    return (
        <Chart_Container style={{ ...prop.style }}>
            <div style={{ position: "relative", width: "100%", height: "100%" }}>
                <Chart
                    style={{
                        background: `conic-gradient(${outerColor} 0% ${chartText}%, ${innerColor} ${chartText}% 100%)`,
                    }}
                >
                    <CenterCircle />
                    <Text style={{ fontSize: prop?.style?.fontSize, display: isHideText ? "none" : "block" }}>{chartText}%</Text>
                </Chart>
            </div>
        </Chart_Container>
    );
}
