import styled from "styled-components";
import PageTemplate from "../component/PageTemplate.tsx";
import ImgBanner from "../assets/img/production/banner.png";
import ImgContent01 from "../assets/img/production/pcms/content-01.png";

import { useContext } from "react";
import { WrapperContext } from "../component/WrapperContext.tsx";

const Main = styled.div`
    width: 100%;
    height: 100%;
    text-align: left;
    padding: ${(props) => (!props.isMobile ? "25px 0 30px 10px" : "25px 0 0 0")};
`;

const Content = styled.img`
    width: 100%;
    margin-bottom: 5vh;
`;
const ContentText = styled.div`
    display: block;
    font-family: SCDream5;
    font-size: 17px;
    width: max-content;
    max-width: ${(props) => (!props.isMobile ? "100%" : "fit-content")};
    margin-left: 5px;
    margin-bottom: 20px;
    line-height: 150%;
`;

export default function PCMS() {
    const { isMobile } = useContext(WrapperContext);
    const count = Array.from({ length: 10 }, (_, index) => String(index + 1).padStart(2, "0"));

    return (
        <PageTemplate bannerImage={ImgBanner}>
            <Main isMobile={isMobile}>
                <ContentText isMobile={isMobile}>
                    플러스프로는 기업이 필요로 하는 시스템에 관한 기획, 개발, 구축과 운영까지 고객의 환경에 적합한
                    <br />
                    토탈지원 서비스로 효율적 운영과 가용성을 보장해 드립니다.
                </ContentText>
                <Content src={ImgContent01} />
                {count.map((cnt) =>
                    !isMobile ? (
                        <Content key={`sample${cnt}`} src={require(`../assets/img/production/pfms/content-${cnt}.png`)} />
                    ) : (
                        <Content key={`sample${cnt}`} src={require(`../assets/img/production/pfms/mo/mo_content-${cnt}.png`)} />
                    ),
                )}
            </Main>
        </PageTemplate>
    );
}
