import styled from "styled-components";
import PageTemplate from "../component/PageTemplate.tsx";
import ImgBanner from "../assets/img/production/banner.png";
import ImgContent02 from "../assets/img/production/plm/content02.png";

import { useContext } from "react";
import { WrapperContext } from "../component/WrapperContext.tsx";
import ImgMoContent02 from "../assets/img/production/plm/mo_content02.png";

const Main = styled.div`
    width: 100%;
    height: 100%;
    text-align: left;
    padding: ${(props) => (!props.isMobile ? "25px 30px 0 10px" : "25px 0 0 0")};
`;

const Content = styled.img`
    width: 100%;
    margin-bottom: 10vh;

    @media screen and (max-width: 750px) {
        margin-bottom: 3vh;
    }
`;

export default function Teamcenter() {
    const { isMobile } = useContext(WrapperContext);

    return (
        <PageTemplate bannerImage={ImgBanner}>
            <Main isMobile={isMobile}>
                <Content src={!isMobile ? ImgContent02 : ImgMoContent02} />
            </Main>
        </PageTemplate>
    );
}
