import styled from "styled-components";
import PageTemplate from "../component/PageTemplate.tsx";
import ImgBanner from "../assets/img/production/banner.png";
import ImgContent01 from "../assets/img/production/plm/content01.png";
import ImgMoContent01 from "../assets/img/production/plm/mo_content01.png";

import { useContext } from "react";
import { WrapperContext } from "../component/WrapperContext.tsx";
const Main = styled.div`
    width: 100%;
    height: 100%;
    text-align: left;
    padding: ${(props) => (!props.isMobile ? "25px 0 30px 10px" : "25px 0 0 0")};
`;

const Content = styled.img`
    width: 100%;
    margin-bottom: 10vh;

    @media screen and (max-width: 750px) {
        margin-bottom: 3vh;
    }
`;

const SubTitle = styled.div`
    font-family: SCDream6;
    font-size: 17px;
`;
const SubTitleLine = styled.hr``;

const SampleWrapper = styled.div``;
const Sample = styled.img`
    width: 220px;
    height: 200px;
    margin: 10px;

    @media screen and (max-width: 750px) {
        // width: 45%;
    }
`;

export default function NX() {
    const { isMobile } = useContext(WrapperContext);
    const count = Array.from({ length: 5 }, (_, index) => String(index + 1).padStart(2, "0"));

    return (
        <PageTemplate bannerImage={ImgBanner}>
            <Main isMobile={isMobile}>
                <Content src={!isMobile ? ImgContent01 : ImgMoContent01} />

                <SubTitle>{"NX 샘플"}</SubTitle>
                <SubTitleLine />
                <SampleWrapper>
                    {count.map((cnt) => (
                        <Sample key={`sample${cnt}`} src={require(`../assets/img/production/plm/nx_sample-${cnt}.png`)} />
                    ))}
                </SampleWrapper>
            </Main>
        </PageTemplate>
    );
}
